:root {
  font-size: 62.5%;
}

body {
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", Roboto,
    "Helvetica Neue", Arial, sans-serif, "Apple Color Emoji", "Segoe UI Emoji",
    "Segoe UI Symbol", "Noto Color Emoji";
  font-size: 1.3rem;
  margin: 0;
  padding: 0 2rem;
}

h1,
h2 {
  font-weight: 400;
}

h1 {
  font-size: 4.2rem;
  margin-bottom: 0;
}

hr {
  border-top: 1px dashed #000;
}

pre,
code {
  background-color: #f0eaff;
}

pre {
  display: inline-block;
  padding: 1rem;
  max-width: calc(100% - 2rem);
  overflow-x: auto;
  border: 1px dashed #b192fd;
}

.contain {
  max-width: 96rem;
  margin: 0 auto;
}

.contain > div {
  width: 50%;
}

.contain > div:first-of-type {
  float: left;
}

.contain > div:last-of-type {
  float: right;
}

@media (max-width: 860px) {
  .contain > div {
    width: 100%;
  }
}
